.center {
  justify-content: center;
  align-items: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 13rem;
  grid-gap: 1px;
  grid-auto-flow: row dense;
  margin: 10px;
  counter-reset: grid;
}
.grid > article {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: var(--dark);
  color: var(--light);
  position: relative;
  counter-increment: grid;
}
.grid > article:before {
  content: counter(grid);
  position: absolute;
  top: 0;
  left: 0;
  background: var(--txt);
  color: var(--light);
  width: 1rem;
  height: 1rem;
  font-size: 10px;
  text-align: center;
}
.grid > article:nth-child(1) {
  grid-column: 1/3;
  grid-row: 1/3;
}
.grid > article:nth-child(7) {
  grid-column: 2/span 2;
  grid-row: 4/span 2;
}
.grid > article:nth-child(11) {
  grid-column: 1/3;
  grid-row: 7/span 2;
}
.grid > article:nth-child(14) {
  grid-column: 3;
  grid-row: 7/span 3;
}
.grid > article:nth-child(20) {
  grid-column: 2/span 2;
  grid-row: 10/span 2;
}
.grid > article:nth-child(24) {
  grid-column: 1/3;
  grid-row: 13/span 2;
}
.grid > article:nth-child(31) {
  grid-column: 2/span 2;
  grid-row: 16/span 2;
}
.grid > article:nth-child(36) {
  grid-column: 2/span 2;
  grid-row: 19/span 1;
}
.grid > article:nth-child(37) {
  grid-column: 1/3;
  grid-row: 20/span 1;
}
.grid > article:nth-child(39) {
  grid-column: 1/span 3;
  grid-row: 21;
}
@media (max-width: 60em) {
  .grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 100px;
    grid-gap: 1px;
    grid-auto-flow: row dense;
    margin: 0px;
  }
  .grid > article {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: stretch;
    overflow: hidden;
    background: var(--dark);
    color: var(--light);
  }
