/** - App
----------------------- */
#app,
.header {
  overflow: hidden;
  position: relative;
}

#app {
  background-color: var(--light);
  margin: auto;
  height: 100%;
  z-index: 2;
  opacity: 1;
  -webkit-animation: oninit 500ms ease-in;
          animation: oninit 500ms ease-in;
}
@-webkit-keyframes oninit {
  from {
    opacity: 0;
  }
}
@keyframes oninit {
  from {
    opacity: 0;
  }
}

/** - Theme Header
----------------------- */
.header {
  min-height: 3em;
  margin-left: 2em;
  margin-right: 2em;
  padding: 2em 1em;
  z-index: 5;
}
.header .logo {
  float: left;
  font-size: 1.3em;
  font-family: var(--font-family);
  font-weight: 700;
  color: var(--txt);
}
.header .navigation {
  float: right;
  font-family: var(--font-family);
}
.header .navigation a {
  text-decoration: none;
  margin-left: 1em;
  color: var(--dark);
  border-bottom: 1px solid var(--light);
  transition: all 0.5s ease;
}
.header .navigation a.active, .header .navigation a:hover {
  color: var(--dark);
  opacity: 0.8;
  border-bottom: 1px solid var(--dark);
  transition: all 0.5s ease;
}
@media (max-width: 768px) {
  .header {
    margin-left: 0;
    margin-right: 0;
  }
  .header.active {
    overflow: auto !important;
    min-height: 100vh;
  }
  .header.active .navigation {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    float: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--dark);
    z-index: 999999;
    text-align: center;
    overflow: auto;
    opacity: 1;
    visibility: visible;
    -webkit-animation: menu 500ms ease-out 1;
            animation: menu 500ms ease-out 1;
    -webkit-clip-path: circle(100% at 50% 50%);
            clip-path: circle(100% at 50% 50%);
    transition: opacity 0.5s ease;
  }
  .header.active .navigation a {
    display: block;
    width: 100%;
    margin: 1em auto;
    font-size: 2em;
    text-align: center;
    color: var(--light);
    border-bottom: 1px solid var(--dark);
  }
  .header.active .navigation a .active, .header.active .navigation a:focus {
    color: var(--light);
  }
  .header.active .navigation a:nth-child(2) {
    margin-top: 3em;
  }
  .header.active a.btn-menu {
    margin: 0;
    width: 2em;
    top: 1em;
    right: 1em;
  }
  .header.active a.btn-menu span {
    transform: translateY(14px) rotate(45deg);
    transition: all 0.5s ease;
    width: 75%;
    background: var(--light);
  }
  .header.active a.btn-menu span + span {
    transform: translateY(1px) rotate(-45deg);
    transition: all 0.5s ease;
    width: 75%;
  }
  .header .navigation {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease;
  }
  .header .navigation a {
    display: none;
  }
  .header .btn-menu {
    width: 2em;
    height: 1em;
    position: absolute;
    top: 1em;
    right: 1em;
    font-size: 2em;
    margin: 0;
    display: block;
  }
  .header .btn-menu span {
    position: absolute;
    top: 0;
    right: 0;
    width: 75%;
    height: 0.05em;
    margin: 0;
    background: var(--dark);
    transition: all 0.5s ease;
  }
  .header .btn-menu span:first-child {
    width: 50%;
  }
  .header .btn-menu span:nth-child(2) {
    top: 0.5em;
  }
  @-webkit-keyframes menu {
    from {
      -webkit-clip-path: circle(0 at 50% 50%);
              clip-path: circle(0 at 50% 50%);
    }
  }
  @keyframes menu {
    from {
      -webkit-clip-path: circle(0 at 50% 50%);
              clip-path: circle(0 at 50% 50%);
    }
  }
}

